import React from 'react';
import { AiFillCheckCircle } from "react-icons/ai";
import { MdErrorOutline } from "react-icons/md";

function NewPost(props){

  if(props.errPost){
    return(
      <React.Fragment>
        <div className='err-new-post'><MdErrorOutline />  xy ra li khi to bi vit. Vui lng th li</div>
      </React.Fragment>
    )
  }

  return(
    <React.Fragment>
      <div className='count-new-post'><AiFillCheckCircle /> {props.newPost.length} bi vit mi  c ng, bn c th nhn thy bi vit ca mnh  trang c nhn ca bn </div>
    </React.Fragment>
  )
}

export default NewPost;
