import React from 'react';
import {Link, useHistory} from 'react-router-dom';
import './index.css';

function NotFound(){
  const history = useHistory();
  const yearNow = new Date().getFullYear();

  const changeActiveKey = (key) => {
    history.push('/rules-mohi', {key: key});
  }

  return(
    <main className="wrapper" style={{marginTop: '64px'}}>
        <div className="main-section">
            <div className="main-section-data">
              <div className="main404">
              <p>
                Rt tic, Ni dung ny  b g hoc khng tn ti
              </p>
              <h1>404</h1>
              <hr/>
              <button type="button"><Link to="/">Tr li trang ch</Link></button>
              </div>
              <div className="footer-not-found">
                <hr/>
                <footer>
                  <ul>
                    <li>
                      <span onClick={() => changeActiveKey(2)}>Chnh sch d liu</span>
                    </li>
                    <li>
                      <span onClick={() => changeActiveKey(1)}>iu khon v dch v</span>
                    </li>
                    <li>
                      <span onClick={() => changeActiveKey(3)}>Tiu chun cng ng</span>
                    </li>
                    <li>
                    <span><Link to="/help">Tr Gip</Link></span>
                    </li>
                  </ul>
                  <div className="mohi">
                    Mohi &copy; {yearNow}
                  </div>
                </footer>
              </div>
            </div>
        </div>
      </main>
  )
}

export default NotFound;
