import React, {useEffect, useState} from 'react';
import {useForm} from 'react-hook-form';
import {RiErrorWarningLine} from "react-icons/ri";
import {Tooltip} from 'antd';

function ForgotPassword(props){
  const {register, handleSubmit, errors, setError} = useForm();
  const [errorEmailAndPhone, setErrorEmailAndPhone] = useState('Vui lng nhp s in thoi hoc email');

  useEffect(() => {
    if(props.error){
      setError("user_email", {
            type: "manual",
            message: "Email hoc s in thoi  c ng k"
          });
      setErrorEmailAndPhone('Email hoc s in thoi cha c ng k')
    }
  },[props.error])

  const onSubmit = (data) => {
    props.onSubmitForgotPass(data)
  }

  return(
    <React.Fragment>
      <div className="veri-code-container">
        <form onSubmit={handleSubmit(onSubmit)}>
          <h2>Qun mt khu</h2>
        <div className="veri-code-message"><span style={{color: 'black'}}>S dng s in thoi hoc email m bn  ng k  ly li mt khu</span></div>
          <div className="submit-error-veri-code"></div>
          <div className="form-veri-code-group">
            <input
              type="text"
              name="user_email"
              placeholder="Nhp tn ti khon"
              ref={register({required:true})}
              style={errors.user_email ? {border: '1px solid red'} : null}
            />
          {errors.user_email && <Tooltip placement="right" title={<p className = "error_sex_tooltip" >{errorEmailAndPhone}</p>}><RiErrorWarningLine className="error_sex error_code"/></Tooltip>}
          </div>
          <div className="form-veri-code-group">
            <button type="submit">Xc nhn</button>
          </div>
        </form>
      </div>
    </React.Fragment>
  )
}

export default ForgotPassword;
